

.photo-edit__form {
  padding: 18px 0;

  background-color: @gray;
}

.photo-edit__load-image {
  display: block;
  .container;
}

.photo-edit__image {

}

.photo-edit__effects {
  position: relative;

  display: flex;
  justify-content: center;
  min-height: 135px;
  padding: 28px 0;

  .font(0;0);
}

.photo-edit__effect-item {
  width: 18%;
  height: 44px;
  margin: 0 16px;

  &--crop .photo-edit__range-toggle {
    left: 83.333%;
  }

  &--fill .photo-edit__range-toggle {
    left: 15.841%;
  }

  &--contrast .photo-edit__range-toggle {
    left: 51%;
  }
}

.photo-edit__effect-btn {
  background-color: @graydark;
  border: none;
  width: 100%;
  height: 100%;

  &--current {
    background-color: @pink;
  }

  &--current ~ .photo-edit__range-control {
    display: block;
  }
}

.photo-edit__range-control {
  position: absolute;
  bottom: 0;
  left: 0;

  display: none;
  width: 100%;
  height: 16px;
  margin-bottom: 28px;

}

.photo-edit__bar {
  position: relative;
  top: 50%;
  bottom: 50%;

  width: auto;
  height: 2px;
  margin: 0 7%;

  background-color: @blue;
  cursor: pointer;
}

.photo-edit__range-toggle {
  position: absolute;

  width: 16px;
  height: 16px;

  border-radius: 50%;
  border: 3px solid @pink;
  background-color: @gray;

  left: 0;
  transform: translate(-50%, -50%);
  cursor: pointer;
}


.photo-edit__btns-actions {
  .container;
  padding-top: 28px;
  position: relative;

  .font(18px; 18px);

  &::before {
    .pseudo(100%;2px);
    top: 0;
    left: 0;

    background-color: @graytoggle;
  }
}

.photo-edit__btn-action {
  width: 100%;
  margin-bottom: 12px;
  padding: 15px 0;

  border-radius: 28px;
  font-weight: 700;
  background-color: transparent;
  border-style: solid;
  border-width: 3px;

  &--post {
    color: @pink;
    border-color: @pink;
  }

  &--cancel {
    color: @graycancel;
    border-color: @graycancel;
  }
}

@media (min-width: @tablet-width) {

  .photo-edit__wrapper {
    display: flex;
  }

  .photo-edit__load-image {
    width: 50%;
  }

  .photo-edit__cover-effects-actions {
    width: 50%;
  }

  .photo-edit__effects {
    display: block;
    padding: 0;
  }



.photo-edit__effect-item {
  position: relative;

  width: 100%;
  display: flex;

  &::before {
    .pseudo(100%;2px);
    bottom: 0;
    left: 0;

    background-color: @graytoggle;
  }
}

  .photo-edit__effect-btn {
    width: 28px;
    height: 28px;
  }


.photo-edit__range-control {
  position: relative;
    bottom: 0;
  left: 0;

  display: block;
}

.photo-edit__btns-actions::before  {
display: none;
}
}

@media (min-width: @desktop-width) {
  .photo-edit__wrapper {
    .container-desk;
  }
}
